.enterprises:not(.enterprises-td):not(.enterprises-bkf):not(.enterprises-scbc) {
  .swiper-button {
    &-prev {
      &:before {
        background: url(../images/svg/arrow-prev-green.svg) center no-repeat;
      }
    }
    &-next {
      &:before {
        background: url(../images/svg/arrow-next-green.svg) center no-repeat;
      }
    }
  }
}
.enterprises {
  .ui-subtitle {
    color: $bgc;

    @media (max-width: 1259px) {
      @include fz(42);
      margin-bottom: 60px;
    }

    @media (max-width: 959px) {
      margin-bottom: 24px;
      @include fz(36);
    }

    @media (max-width: 599px) {
      @include fz(28);
    }
  }
  &-circle {
    margin-bottom: 40px;

    @media (max-width: 1259px) {
      margin-bottom: 0;
      margin-left: 40px;
    }

    @media (max-width: 959px) {
      vertical-align: top;
      margin-left: 0;
      margin-right: 12px;
    }
  }
  &-info {
    padding-top: 80px;
  }
  &-block {
    padding-top: 80px;
    padding-bottom: 120px;

    @media (max-width: 959px) {
      padding-bottom: 40px;
      padding-top: 40px;
    }
  }
  &-num {
    &-item {
      @include fz(28);
      margin-bottom: 80px;
      padding-right: 8.32%;

      &-top {
        @include fz(110);
        color: $secondary;
   
        line-height: 1;
        @media (max-width: 1439px) {
          @include fz(90);
        }
      }

      @media (max-width: 1439px) {
        @include fz(24);
      }

      @media (max-width: 959px) {
        @include fz(16);
        margin-bottom: 20px;

        &-top {
          @include fz(44);
        }
      }

      @media (max-width: 599px) {
        margin-bottom: 12px;

        &-top {
          @include fz(32);
        }
      }
    }

    @media (max-width: 599px) {
      .col-6 {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }

  &-proud {
    background: $secondary;
    color: #fff;
    padding-bottom: 40px;
    // padding-bottom: 120px;

    .ui-subtitle {
      color: #fff;
    }

    &-item {
      padding-right: 24%;
      margin-bottom: 80px;
    }
    &-img {
      margin-bottom: 25px;
      height: 88px;
      display: flex;
      align-items: flex-end;

      img {
        max-height: 88px;
      }
    }
    &-text {
      @include fz(22);
      @media (max-width: 1259px) {
        @include fz(18);
      }
    }

    @media (max-width: 959px) {
      &-item {
        padding-right: 10%;
        margin-bottom: 32px;
      }
      &-img {
        height: 45px;
        margin-bottom: 16px;

        img {
          max-height: 45px;
        }
      }
      &-text {
        @include fz(16);
      }
    }
    @media (max-width: 599px) {
      padding-bottom: 30px;

      .col-6 {
        flex: 0 0 100%;
        max-width: 100%;
      }
      &-item {
        padding-right: 0;
        margin-bottom: 24px;
        display: flex;
      }
      &-img {
        flex: 0 0 72px;
        max-width: 72px;
        margin-right: 16px;
        height: auto;
        align-items: flex-start;
      }
      &-text {
        @include fz(12);
      }
    }
  }
  &-sort {
    .ui-subtitle {
      @media (max-width: 1259px) {
        display: inline-block;
        max-width: 274px;
        margin-bottom: 0;
      }
    }
    &-adaptive {
      margin-bottom: 60px;
      &-link {
        display: none;

        @media (max-width: 1259px) {
          display: flex;
          align-items: center;
        }
      }
      @media (max-width: 1259px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }

    @media (max-width: 959px) {
      &-adaptive {
        margin-bottom: 24px;
        display: block;

        &-link {
          display: block;
          overflow-x: auto;
          white-space: nowrap;
          margin-right: -15px;
          margin-left: -15px;
          padding-left: 15px;
          padding-right: 15px;
          max-width: calc(100% + 30px);
          scrollbar-width: 0;

          &::-webkit-scrollbar {
            width: 0;
            height: 0;
            -webkit-appearance: none;
          }

          * {
            white-space: normal;
          }
        }
      }

      .ui-subtitle {
        max-width: 100%;
        display: block;
        margin-bottom: 24px;
      }
    }
  }
  &-diplom {
    .ui-subtitle {
      color: $bgc;
    }
  }
  &-contact {
    background: #222e3f;
    padding-top: 80px;
    padding-bottom: 120px;

    &-container {
      margin-bottom: 60px;
    }

    &-adress {
      @include fz(28);
      padding-right: 10.86%;

      a {
        color: #FFF;

        &:hover {
          color: $secondary;
        }
      }
      &-title {
        color: $secondary;
        margin-bottom: 24px;
        font-weight: 700;
      }
      &-text {
        color: #fff;
        margin-bottom: 24px;
      }
      &-link {
        display: block;
        position: relative;
        max-width: 336px;
        padding: 24px 30px;
        color: #fff;
        @include fz(24);
        border: 1px solid #fff;
        border-radius: 8px;

        .docs-card-icon {
          opacity: 1;
          stroke: #fff;
          transform: translate(0%, 50%);
          bottom: 50%;
          right: 30px;
          transition: 0.3s ease;
        }
        &:hover {
          border: 1px solid $secondary;
          color: $secondary;
          .docs-card-icon {
            stroke: $secondary;
          }
        }
      }
      @media (max-width: 1259px) {
        @include fz(24);
      }
    }
    .ui-subtitle {
      color: $secondary;
    }
    .node-slider-comm-body {
      padding-left: 0;
      max-width: 66.56%;
      @media (max-width: 1439px) {
        padding-right: 15%;
      }
    }
    .node-slider-comm-img {
      max-width: 33.43%;
    }
    .museum-review-position {
      color: #fff;
      @include fz(28);
      margin-bottom: 24px;
      font-weight: 700;
      @media (max-width: 1259px) {
        @include fz(24);
      }
    }
    .museum-review-author {
      color: $secondary;
      @include fz(54);
      margin-bottom: 24px;
      font-weight: 400;
      line-height: 1;
      @media (max-width: 1259px) {
        @include fz(42);
      }
    }
    .museum-review-txt {
      @include fz(22);
      font-weight: 600;
      color: #fff;
      @media (max-width: 1259px) {
        @include fz(18);
      }
    }

    @media (max-width: 959px) {
      padding-top: 40px;
      padding-bottom: 40px;

      &-adress {
        padding-right: 0;
        margin-bottom: 40px;
        @include fz(20);

        &-title {
          display: none;
        }
        &-link {
          width: 284px;
          max-width: 100%;
          padding: 11px 14px;
          @include fz(16);

          .docs-card-icon {
            right: 14px;
            stroke-width: 1;
          }
        }
        &-text {
          margin-bottom: 20px;
        }
      }
      &-container {
        margin-bottom: 0;
      }
      .node-slider-comm {
        &-body {
          padding-right: 24px;
          max-width: calc(100% - 200px);
        }
        &-img {
          flex: 0 0 200px;
          max-width: 200px;
        }
      }
      .museum-review {
        margin-top: 0;

        &-position {
          @include fz(18);
          margin-bottom: 12px;
        }
        &-author {
          @include fz(24);
          margin-bottom: 2px;
        }
        &-txt {
          @include fz(16);
          font-weight: 400;
        }
      }
    }

    @media (max-width: 599px) {
      &-adress {
        @include fz(14);

        &-link {
          width: 100%;
          @include fz(14);
        }
      }

      .node-slider-comm {
        align-items: flex-start;

        &-body {
          padding-right: 8px;
          max-width: calc(100% - 128px);
        }
        &-img {
          flex: 0 0 128px;
          max-width: 128px;
          padding-top: 48px;
        }
      }
      .museum-review {
        margin-top: 0;

        &-position {
          @include fz(16);
          margin-bottom: 24px;
          margin-right: -128px;
        }
        &-author {
          @include fz(20);
          margin-bottom: 2px;
        }
        &-txt {
          @include fz(14);
          font-weight: 400;
        }
      }
    }
  }
  &-news {
    background: $secondary;
    .ui-side-title {
      color: #fff;
    }
    .news-item-date {
      color: $bgc;
    }
  }
  .museum-slider-btns .swiper-button-next {
    background-image: url(../images/svg/slider-arrow-next.svg);

    &:before {
      filter: none;
    }
  }
  .museum-slider-btns .swiper-button-prev {
    background-image: url(../images/svg/slider-arrow-prev.svg);

    &:before {
      filter: none;
    }
  }
  .enterprises-circle-circle {
    background: $secondary;
  }
}
.circle-flex {
  display: flex;
  flex-direction: column;
  @media (max-width: 1259px) {
    display: none;
  }
}

.enterprises-swipper {
  position: relative;

  .swiper-slide {
    display: flex;
    align-items: flex-end;
    text-align: left;
  }
  .enterprises-slider-text {
    padding: 0 24px;
  }

  .ui-tags-inline {
    margin-right: 0;
  }
  .ui-subtitle {
    color: $bgc;
  }
}

.history-header-top {
  padding-top: 100px;
}
.history-header-main {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.slider-text {
  @include fz(28);
  margin-bottom: 40px;


  @media (max-width: 1259px) {
    @include fz(24);
  }

  @media (max-width: 959px) {
    @include fz(20);
    margin-bottom: 12px;
  }
  @media (max-width: 599px) {
    @include fz(14);
  }
}

.enterprises-contact {
  &-tabs {
    display: none;
    font-size: 0;
  }
  &-tab {
    display: inline-block;
    margin-right: 8px;
    height: 50px;
    line-height: 48px;
    padding: 0px 24px;
    border-radius: 25px;
    border: 1px dashed rgba(#FFF,.5);
    font-weight: 600;
    @include fz(16);
    color: #FFF;

    &.selected,
    &:hover {
      border: 1px solid $secondary;
      color: $secondary;
    }
  }

  @media (max-width: 959px) {
    &-tabs {
      display: block;
      overflow-x: auto;
      margin-bottom: 20px;
      white-space: nowrap;
      scrollbar-width: 0;
      margin-right: -15px;
      margin-left: -15px;
      padding-left: 15px;
      padding-right: 15px;

      &::-webkit-scrollbar {
        width: 0;
        height: 0;
        -webkit-appearance: none;
      }
    }
  }

  @media (max-width: 599px) {
    &-tab {
      height: 40px;
      line-height: 38px;
      @include fz(14);
    }
  }
}


.enterprises {
  background-color: #CCE0DE;
  color: $bgc;
  margin-bottom: -30px;

  @media (max-width: 959px) {
