.enterprises-activity {
  display: flex;
  flex-direction: row;

  &-block {
    padding-right: 9.8%;
  }
  &-title {
    margin-bottom: 54px;
    @include fz(42);
    color: $bgc;

    @media (max-width:1259px) {
      @include fz(32);
    }
  }
  &-items {
    display: flex;
    max-width: 320px;
    margin-bottom: 60px;
    cursor: pointer;

    &:last-child {
      margin-bottom: 0;
    }
   
    &-item {
      position: relative;
      // padding: 10px;
      
      &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border: 1px solid #F08322;
        border-radius: 50%;
        z-index: 1;
        opacity: 0;
        transition: transform .5s, opacity .5s;
      }
    }
    &-img {
      height: 140px;
      width: 140px;
      border-radius: 50%;
      overflow: hidden;

      img {
        position: relative;
        bottom: -2px;
      }
    
      
      @media (max-width:1439px) {
        height: 100px;
        width: 100px;
      }
    }
    &-text {
      @include fz(26);
      color: #DD722A;
      max-width: 155px;
      margin-left: 14px;
      line-height: 1.15;

      @media (max-width:1439px) {
        @include fz(22);
      }
    }

    &:hover .enterprises-activity-items-item::after {
      opacity: 1;
      transform: scale(1.12);
    }
  }
  .activity-primary {
    &-text {
      color: $primary;
    }
    &::after {
      border-color: $primary;
    }
  }
  .activity-tertiary {
    &-text {
      color: $tertiary;
    }
    &::after {
      border-color: $tertiary;
    }
  }
  .activity-brown {
    &-text {
      color: $brown;
    }
    &::after {
      border-color: $brown;
    }
  }
  .activity-blue {
    &-text {
      color: $blue;
    }
    &::after {
      border-color: $blue;
    }
  }
}

.enterprises-activity-items-item {
  border-radius: 100px;
  flex: 0 0 140px;
  max-width: 140px;

  @media (max-width: 1439px) {
    flex: 0 0 100px;
    max-width: 100px;
  }

  &.ui-bg-primary,
  &.ui-bg-orange {
    &::after {
      border-color: $primary;
    }
  }

  &.ui-bg-secondary,
  &.ui-bg-green {
    &::after {
      border-color: $secondary;
    }
  }

  &.ui-bg-tertiary,
  &.ui-bg-lime {
    &::after {
      border-color: $tertiary;
    }
  }

  &.ui-bg-blue {
    &::after {
      border-color: $blue;
    }
  }

  &.ui-bg-brown {
    &::after {
      border-color: $brown;
    }
  }

  &.ui-bg-darkyellow {
    &::after {
      border-color: $darkyellow;
    }
  }

  &.ui-bg-lightorange {
    &::after {
      border-color: $lightorange;
    }
  }
}


.enterprises-tabs {
  padding-top: 80px;
  padding-bottom: 80px;

  @media (max-width: 959px) {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

.ui-tabs-content {
  .enterprises-block:first-child {
    padding-top: 0;
  }
}
.enterprises-content {
  &-block {
    padding-bottom: 60px;

    // @media (max-width:1259px) {
    //   padding-bottom: 120px;
    // }
    @media (max-width: 959px) {
      padding-bottom: 32px;
    }
  }
  &-title {
    @include fz(54);
    font-weight: 600;
    color: #268E80;
    word-break: break-word;
    line-height: 1;
    margin-bottom: 40px;

    &-big {
      @include fz(80);
      font-weight: 400;
    }

    @media (max-width: 1439px) {
      &-big {
        @include fz(54);
      }
    }

    @media (max-width: 1259px) {
      @include fz(42);
    }

    @media (max-width: 959px) {
      margin-bottom: 24px;
      @include fz(36);

      &-big {
        @include fz(48);
        margin-bottom: 24px;
      }
    }

    @media (max-width: 599px) {
      @include fz(28);

      &-big {
        @include fz(32);
      }
    }
  }
}

.enterprises {
  .news-item {
    &-title-arrow {
      stroke: $bgc;
    }

    &:hover {
      .news-item {
        &-title {
          color: $bgc;
        }
      }
    }
  }

  .to-all-news a {
    &:hover {
      color: $bgc;
      
      .news-item-title-arrow {
        stroke: $bgc;
      }
    }
  }
}

.enterprises-block {
  .node-inner {
    padding-right: 0;
  }
}
.enterprises-activity {
  display: flex;

  &-block {
    flex: 0 0 33.33%;
    max-width: 33.33%;
    padding-right: 8%;
  }
  &-items {
    figure {
      margin: 0;
    }
    figcaption {
      display: none;
      visibility: hidden;
    }
  }

  @media (max-width: 959px) {
    display: block;

    &-block {
      max-width: 100%;
      max-width: calc(100% + 24px);
      margin-bottom: 24px;
      padding-right: 0;
      font-size: 0;
      margin-right: -24px;
    }
    &-title {
      @include fz(32);
      margin-bottom: 16px;
    }
    &-items {
      margin-bottom: 0;
      display: inline-block;
      vertical-align: top;
      margin-right: 24px;
      text-align: center;
      width: 144px;
      max-width: calc(33.33% - 24px);

      &-text {
        @include fz(18);
        margin-left: 0;
      }
      &-item {
        margin-bottom: 4px;
        width: 100%;
        max-width: 100%;
      }
      &-img {
        width: 100%;
        height: 0;
        padding-bottom: 100%;

        img {
          width: 100%;
        }
      }
    }
    &-container {
      display: block !important;
    }
  }

  @media (max-width: 599px) {
    &-title {
      @include fz(24);
      margin-bottom: 16px;
    }
    &-block {
      margin-right: -16px;
      max-width: calc(100% + 16px);
    }
    &-items {
      max-width: calc(33.33% - 16px);
      margin-right: 16px;

      &-text {
        @include fz(14);
      }
    }
  }
}